<template>
  <div class="header">
    <div class="menu">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="编辑" name="/admin/editor"></el-tab-pane>
        <el-tab-pane label="文章管理" name="/admin/article_mgt"></el-tab-pane>
        <el-tab-pane label="返回前台" name="/"></el-tab-pane>
      </el-tabs>
    </div>
    <div @click="logout" class="logout">退出</div>
    <router-view></router-view>
  </div>
</template>
<script>
  import util from '../../utils/utils'
  export default {
    data() {
      return {
        activeName: '/admin/editor'
      };
    },
    mounted() {
      util.$on('editorTabActive',()=>{
        this.activeName = '/admin/editor'
      })
      util.$on('articleMgtTabActive',()=>{
        this.activeName = '/admin/article_mgt'
      })
    },
    methods: {
      //tab, event
      handleClick() {
        if (this.$route.query.type == 'editor') {
          this.$router.push({path: this.activeName}).catch(() =>{})
          util.$emit('activatePublish') // 激活发布
          return
        }
        this.$router.push({path: this.activeName}).catch(() =>{})
      },
      logout() {
        localStorage.setItem('token', '')
        this.$router.push({path: '/login'});
      },
    }
  };
</script>

<style scoped>
.menu {
  margin-bottom: 10px;
  width: 100%
}
.logout {
  position: absolute;
  right: 50px;
  top: 10px;
}
.logout:hover{
  color: #409EFF;
  cursor: pointer;
}
</style>
